<template>
	<div class="homeLayer layer">
		<mo-navBar :nowPage="nowPage"></mo-navBar>
		<div class="main">
			<mo-packItems :packData="packData"></mo-packItems>
			<mo-videoItems :videoData="videoData"></mo-videoItems>
		</div>
	</div>
</template>

<script>
	import NavBar from './module/NavBar.vue'
	import PackItems from './module/PackItems.vue'
	import VideoItems from './module/VideoItems.vue'

	export default{
		name: 'Home',
		components: {
			'mo-navBar': NavBar,
			'mo-packItems': PackItems,
			'mo-videoItems': VideoItems
		},
		data(){
			return {
				nowPage: 'Home',
				packData: [{
					isTitleShow: '新鲜主题',
					items: [1, 2, 3, 4]
				}],
				videoData: {
					isTitleShow: '精选内容',
					items: [1, 2, 3, 4, 1, 2, 3, 4]
				}
			}
		},
		methods: {
			navTo: function(result) {
				this.$router.push('/' + result.id);
			},
			choose: function(result) {
				console.log(result);
				// alert('你按回车键了');
				// if (result.keyCode === 13) {
				// }
			}
		},
		beforeRouteEnter: function(to, from, next) {
			console.log('beforeRouteEnter');
			next();
		},
		beforeRouteLeave: function(to, from, next) {
			console.log('beforeRouteLeave');
			next();
		},
		beforeDestroy () {
			console.log('beforeDestroy');
		},
		created: function(){
			console.log('created');
			var that = this;
			document.onkeydown = function(){
				that.choose();
			}
		},
		mounted() {
			console.log('mounted');
		},
		activated() {
			console.log('activated');
		},
		deactivated() {
			console.log('deactivated');
		}
	}
</script>

<style scoped>
</style>



