<template>
	<div class="concernLayer layer">
		<mo-navBar :nowPage="nowPage"></mo-navBar>
		<div class="main">
			<mo-videoItems :videoData="videoData"></mo-videoItems>
		</div>
	</div>
</template>

<script>
	import NavBar from './module/NavBar.vue'
	import VideoItems from './module/VideoItems.vue'

	export default{
		name: 'Concern',
		components: {
			'mo-navBar': NavBar,
			'mo-videoItems': VideoItems
		},
		data(){
			return {
				nowPage: 'Concern',
				videoData: {
					isTitleShow: '',
					items: [1, 2, 3, 4, 1, 2, 3, 4]
				}
			}
		},
		methods: {
		},
		beforeRouteEnter: function(to, from, next) {
			console.log('beforeRouteEnter');
			next();
		},
		beforeRouteLeave: function(to, from, next) {
			console.log('beforeRouteLeave');
			next();
		},
		beforeDestroy () {
			console.log('beforeDestroy');
		},
		created: function(){
			console.log('created');
		},
		mounted() {
			console.log('mounted');
		},
		activated() {
			console.log('activated');
		},
		deactivated() {
			console.log('deactivated');
		}
	}
</script>

<style scoped>
</style>



