<template>
	<div class="videoWrap wrap" v-if="videoData.items && videoData.items.length > 0">
		<h2 class="mainTitle" v-if="videoData.isTitleShow">{{ videoData.isTitleShow }}</h2>
		<div class="videoItems">
			<template v-for="item in videoData.items">
				<div class="video-item" v-tap="{ methods : navTo, id: '123' }">
					<div class="cover" style="background: url(../static/image/d01.jpg);background-size:cover;"></div>
					<div class="info">
						<div class="infoHd">
							<div class="avatar">
								<img src="../../../static/image/avatar.png">
							</div>
							<p><i>#</i>内容标题</p>
						</div>
						<div class="infoBd">
							<p class="infoDesc">秋天，最重要的是吃大闸蟹</p>
						</div>
					</div>
				</div>
			</template>
		</div>
	</div>
</template>

<script>
export default{
	props: {
		videoData: Object
	},
	data() {
		return {
		}
	},
	computed: {
	},
	created: function() {
		var _that = this;
	},
	methods: {
		navTo: function(result) {
			this.$router.push('/' + result.id);
		}
	}
}
</script>

<style scoped>
	.videoWrap .videoItems .video-item {
		position: relative;
		width: 100%;
		height: 15vw;
		overflow: hidden;
		background: rgba(255,255,255,.3);
		margin-bottom: .22rem;
		border-radius: .06rem;
		cursor: pointer;

		transition: all .2s ease;
		-moz-transition: all .2s ease;
		-webkit-transition: all .2s ease;
		-o-transition: all .2s ease;
	}
	.videoWrap .videoItems .video-item:hover {
		background: rgba(240,240,240,.8);
		box-shadow: 0 .4rem 2rem rgba(68,79,99,.8);
		z-index: 5;

		transition: all .2s ease;
		-moz-transition: all .2s ease;
		-webkit-transition: all .2s ease;
		-o-transition: all .2s ease;
	}
	.videoWrap .videoItems .video-item .cover {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		width: 38.5%;
	}
	.videoWrap .videoItems .video-item .info {
		height: 100%;
		padding: 2.8vw 4vw 2vw 52%;
	}
	.videoWrap .infoHd {
		margin-bottom: .22rem;
	}
	.videoWrap .infoHd .avatar {
		display: inline-block;
		width: .9rem;
		height: .9rem;
		border-radius: 5rem;
		overflow: hidden;
		margin-right: .16rem;
		vertical-align: middle;
		font-size: 0;
	}
	.videoWrap .infoHd .avatar img {
		width: 100%;
	}
	.videoWrap .infoHd p {
		display: inline-block;
		vertical-align: middle;
		font-size: .46rem;
		vertical-align: middle;
	}
	.videoWrap .infoHd p i {
		font-style: normal;
		margin-right: .1rem;
		font-size: .5rem;
		vertical-align: middle;
		font-weight: bolder;
	}
	.videoWrap .infoDesc {
	    font-size: .4rem;
		display: -webkit-box;
		overflow: hidden;
		text-overflow: ellipsis;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 1;
		/*letter-spacing: .03rem;*/
	}
</style>



