<template>
	<div class="navWrap">
		<div class="content">
			<p :class="nowPage=='Home' ? 'active icon-tj2' : 'icon-tj'" v-tap="{ methods:toNav, nav:'' }">
				<span>推荐</span>
			</p>
			<p :class="nowPage=='Discovery' ? 'active icon-fx2' : 'icon-fx'" v-tap="{ methods:toNav, nav:'Discovery' }">
				<span>发现</span>
			</p>
			<p :class="nowPage=='Concern' ? 'active icon-dy2' : 'icon-dy'" v-tap="{ methods:toNav, nav:'Concern' }">
				<span>订阅</span>
			</p>
		</div>
	</div>
</template>

<script>
export default{
	props: {
		nowPage: String
	},
	data() {
		return {
		}
	},
	methods: {
		toNav: function(result){
			this.$router.push('/' + result.nav);
		}
	}
}
</script>

<style scoped>
	.navWrap {
		display: -webkit-box;
		display: -webkit-flex;
		display: flex;
		position: fixed;
		width: 22.5vw;
		text-align: center;
		top: 0;
		left: 0;
		bottom: 0;
		-webkit-box-align: center;
		-webkit-align-items: center;
		align-items: center;
		-webkit-box-pack: center;
		-webkit-justify-content: center;
		justify-content: center;
		z-index: 1;
		flex: 1;
		flex-direction: row;
		padding-top: .17rem;
	}
	.navWrap .content p {
		position: relative;
		font-size: .41rem;
		margin: .41rem;
		cursor: pointer;
		padding: .1rem .2rem;
		border-radius: 5rem;
		color: #435e83;

/*		transition: all .2s ease;
		-moz-transition: all .2s ease;
		-webkit-transition: all .2s ease;
		-o-transition: all .2s ease;*/
	}
	.navWrap .content p.active, .navWrap .content p:hover {
		background: url(../../assets/image/navCover.jpg) no-repeat;
		background-size: 100% 100%;
		color: #FFF;

		transition: all .2s ease;
		-moz-transition: all .2s ease;
		-webkit-transition: all .2s ease;
		-o-transition: all .2s ease;
	}
	.navWrap .content p.active:after, .navWrap .content p:hover:after {
		content: '';
		position: absolute;
		top: 60%;
		left: 5%;
		width: 90%;
		height: 90%;
		background-color: rgba(49,69,106,.9);
		border-radius: 5rem;
		z-index: -1;
		transform: scale(.95, .95);
		filter: blur(8px);
		opacity: .6;
	}
	.navWrap .content p:before {
		vertical-align: middle;
	}
	.navWrap .content p span {
		font-size: 0;
	}
	.navWrap .content p.active span, .navWrap .content p:hover span {
		display: inline-block;
		font-size: .25rem;
		margin-left: .12rem;
		vertical-align: middle;
		letter-spacing: .05rem;
	}
</style>



