<template>
	<div class="packWraps">
		<template v-for="packs in packData">
			<div class="packWrap wrap" v-if="packs.items && packs.items.length > 0">
				<h2 class="mainTitle" v-if="packs.isTitleShow">{{ packs.isTitleShow }}</h2>
				<div class="packScroll">
					<div class="packItems" :style="calcWidth(packs.items.length)">
						<template v-for="item in packs.items">
							<div class="pack-item" v-tap="{ methods : navTo, id: '123' }">
								<div class="pack-bg" style="background:url(./static/image/s01.jpg);background-size:cover;"></div>
								<p><i style="background:red;">#</i>每日科技小报<br>by zealer</p>
							</div>
						</template>
					</div>
				</div>
			</div>
		</template>
	</div>
</template>

<script>
export default{
	props: {
		packData: Array
	},
	data() {
		return {
		}
	},
	computed: {
	},
	created: function() {
		var _that = this;
	},
	methods: {
		navTo: function(result) {
			this.$router.push('/' + result.id);
		},
		calcWidth: function (len) {
			return 'width: ' + 17.5 * len + 'vw'
		}
	}
}
</script>

<style scoped>
	.packWrap .packScroll {
		width: 100%;
	}
	.packWrap .packItems {
		width: 100%;
	}
	.packWrap .packItems .pack-item {
		position: relative;
		display: inline-block;
		width: 16.5vw;
		height: 13vw;
		margin-right: 1vw;
		text-align: center;
		letter-spacing: .04rem;
		cursor: pointer;

		transition: all .2s ease;
		-moz-transition: all .2s ease;
		-webkit-transition: all .2s ease;
		-o-transition: all .2s ease;
	}
	.packWrap .packItems .pack-item .pack-bg {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		border-radius: .06rem;
		overflow: hidden;
	}
	.packWrap .packItems .pack-item .pack-bg:before {
		content: '';
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		height: 1rem;
		background: url(../../assets/image/mask_bg.png) repeat-x;
		background-size: 1px 100%;
	}
	.packWrap .packItems .pack-item:last-child {
		margin-right: 0;
	}
	.packWrap .packItems .pack-item:hover {
		position: relative;
		transform: scale(1.1, 1.1);
		z-index: 2;

		transition: all .2s ease;
		-moz-transition: all .2s ease;
		-webkit-transition: all .2s ease;
		-o-transition: all .2s ease;
	}
	.packWrap .packItems .pack-item:hover:after {
		content: '';
		position: absolute;
		left: 10%;
		right: 10%;
		height: 30%;
		bottom: -15%;
		background: rgba(68,79,99,.8);
		filter: blur(18px);
		border-radius: 1rem;
		z-index: -1;
	}
	.packWrap .packItems .pack-item p {
		position: absolute;
		left: 0;
		right: 0;
		bottom: .2rem;
		color: #FFF;
		font-size: .3rem;
		line-height: 1.5;
		letter-spacing: 0;
		vertical-align: middle;
		display: -webkit-box;
		overflow: hidden;
		text-overflow: ellipsis;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
	}
	.packWrap .packItems .pack-item p i {
		display: inline-block;
		width: .28rem;
		height: .28rem;
		line-height: .28rem;
		font-size: .22rem;;
		background: red;
		border-radius: 5rem;
		font-style: normal;
		padding-left: .01rem;
		margin-right: .12rem;
		vertical-align: middle;
	}
</style>



