<template>
	<div class="discoveryLayer layer">
		<mo-navBar :nowPage="nowPage"></mo-navBar>
		<div class="main">
			<mo-packItems :packData="packData"></mo-packItems>
		</div>
	</div>
</template>

<script>
	import NavBar from './module/NavBar.vue'
	import PackItems from './module/PackItems.vue'

	export default{
		name: 'Discovery',
		components: {
			'mo-navBar': NavBar,
			'mo-packItems': PackItems
		},
		data(){
			return {
				nowPage: 'Discovery',
				packData: [{
					isTitleShow: '新鲜主题1',
					items: [1, 2]
				}, {
					isTitleShow: '新鲜主题2',
					items: [1, 2, 3, 4, 5]
				}, {
					isTitleShow: '新鲜主题3',
					items: [1, 2, 3, 4, 5]
				}, {
					isTitleShow: '新鲜主题4',
					items: [1, 2, 3, 4, 5]
				}, {
					isTitleShow: '新鲜主题5',
					items: [1, 2, 3, 4, 5]
				}]
			}
		},
		methods: {
		},
		beforeRouteEnter: function(to, from, next) {
			console.log('beforeRouteEnter');
			next();
		},
		beforeRouteLeave: function(to, from, next) {
			console.log('beforeRouteLeave');
			next();
		},
		beforeDestroy () {
			console.log('beforeDestroy');
		},
		created: function(){
			console.log('created');
		},
		mounted() {
			console.log('mounted');
		},
		activated() {
			console.log('activated');
		},
		deactivated() {
			console.log('deactivated');
		}
	}
</script>

<style scoped>
</style>



