import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/components/Home'
import Concern from '@/components/Concern'
import Discovery from '@/components/Discovery'
import Detail from '@/components/Detail'

Vue.use(Router)

export default new Router({
	mode: 'hash',
	routes: [
		{
			path: '/',
			name: 'Home',
			component: Home
		},
		{
			path: '/Concern',
			name: 'Concern',
			component: Concern
		},
		{
			path: '/Discovery',
			name: 'Discovery',
			component: Discovery
		},
		{
			path: '/:id',
			name: 'Detail',
			component: Detail
		},
		{
			path: '*',
			redirect: '/'
		}
	]
})



